import React from "react";
import { HeadFC, HeadProps, PageProps, graphql } from "gatsby";

import { IBlogByHashtagPageContext, IBlogPageData } from "../types/blog.type";
import { Theme } from "../types/common.type";

import Banner from "../components/common/banner";
import ContentContainer from "../components/common/contentContainer";
import PostPreview from "../components/posts/postPreview";
import Pagination from "../components/common/pagination";
import { HeadSeo } from "../components/common/metadata";

const BlogByHashtag = ({
  data,
  pageContext,
}: PageProps<IBlogPageData, IBlogByHashtagPageContext>) => {
  const posts = data.allMarkdownRemark.nodes;
  const { currentPage, pagesQuantity, markupData, lang, hashtag } = pageContext;

  return (
    <>
      <Banner
        heightClass="lg:h-99"
        titleBlockClassName="w-full"
        title={markupData.blogByHashtagBannerTitle.toUpperCase()}
        borderBottom
      >
        <p className="text-xl lg:text-3xl text-secondary-10 border border-solid border-secondary-10 rounded-full px-8 py-4 lg:px-10 lg:py-5">
          #{hashtag}
        </p>
      </Banner>
      <ContentContainer className="flex-col items-center p-6 md:p-12 gap-14">
        {posts
          ? posts.map((post) => (
              <PostPreview
                theme={Theme.dark}
                key={post.id}
                post={post}
                markupData={markupData}
                lang={lang}
              />
            ))
          : null}
        <Pagination
          section={`blog/${hashtag.split(" ").join("-").toLowerCase()}`}
          pagesQuantity={pagesQuantity}
          currentPage={currentPage}
          nextPageTitle={pageContext.pagination.next}
        />
      </ContentContainer>
    </>
  );
};

export default BlogByHashtag;

export const Head: HeadFC<IBlogPageData, IBlogByHashtagPageContext> = ({
  pageContext: { markupData, currentPage, site, lang, hashtag },
  location,
}: HeadProps<IBlogPageData, IBlogByHashtagPageContext>) => {
  return (
    <HeadSeo
      title={`${markupData.head} | ${hashtag.toUpperCase()} | ${currentPage}`}
      description={site.description}
      siteUrl={site.siteUrl}
      path={location.pathname}
      lang={lang}
    />
  );
};

export const query = graphql`
  query ($skip: Int!, $limit: Int!, $lang: String, $hashtag: String) {
    allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "posts" }, language: { eq: $lang } }
        frontmatter: { keywords: { in: [$hashtag] } }
      }
      sort: { frontmatter: { date: DESC } }
      limit: $limit
      skip: $skip
    ) {
      nodes {
        id
        frontmatter {
          date(formatString: "DD MMM YYYY", locale: $lang)
          title
          text
          cover {
            childImageSharp {
              gatsbyImageData(
                formats: [AUTO, WEBP, AVIF]
                placeholder: BLURRED
                width: 600
              )
            }
          }
        }
        wordCount {
          words
        }
      }
    }
  }
`;
